:root{

    --bgCard : #F4F4F4;
    --textCard : #212121

    }
    



#detailBook {
    background-color: var(--bgCard);
    position: fixed;
    top: 0;right: 0;left: 0;bottom: 0;
    width: 100vw;height: 100vh;
    z-index: 1000;
    overflow: scroll;
}

.closeIcon {
    position: absolute;top:1rem;left:1.3rem;cursor: pointer;opacity: 0.7;

}

.closeIcon:hover{
    opacity: 1;
}

.detailbookBox{
    margin-top: 1rem;
    background-color: var(--bgCard);
    box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    -webkit-box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    border:solid 1px #eee;
    padding: 2rem;
}