:root{

    --bgCard : #f5f5f5;
    --textCard : #bdbdbd;
    --textCardDark :#37474f;
    --bgcontent : #006064

    
    }
    


#ReadBook{
    position: fixed;
    top:0;left: 0;right: 0;bottom: 0;
    background-color: var(--bgCard);
    padding: 1rem 3.5rem 1rem 3.5rem;
    display: flex;flex-direction: column;
    justify-content: flex-start;
    gap: 10px;
    overflow: auto;z-index: 10000;

}



#ReadBook p {
    background-color:var(--bgcontent); 
    border-radius: 5px;
    color: var(--bgCard);
    text-align: right;
    padding: 1rem;letter-spacing: .1rem;
    line-height: 4rem; 

}

.nomorAyat {
    padding: 1rem;
    font-size:23px ;
    color: var(--bgCard) !important;
    display: block;
    text-align: center;
}

#ReadBook span {
    margin:.5rem .2rem 1rem .2rem;
    display: inline-block;
    color :var(--textCardDark)
}

#ReadBook .CloseIcon{
    position: fixed;
    top:5px;
    left: 16px;
    ;padding: 1px;border-radius: 3px;
    color: var(--textCardDark);
    transition: ease-in-out .3s;

}

#ReadBook .CloseIcon:hover{
  color: #666;
  
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  #ReadBook {
    scrollbar-width: auto;
    scrollbar-color: #f2f2f2 #F5F5F5;
  }

  /* Chrome, Edge, and Safari */
  #ReadBook::-webkit-scrollbar {
    width: 6px;
  }

  #ReadBook::-webkit-scrollbar-track {
    background: var(--bgCard);
  }

  #ReadBook::-webkit-scrollbar-thumb {
    background-color: var(--bgCard);
    border-radius: 4px;
  }


  

  /* Responsive Mobile */


